import classNames from 'classnames';

type TitleProps = {
  text: string
  extraClassName?: string
};

export const Title = ({ text, extraClassName }: TitleProps) => {
  const titleClasses = classNames('title', extraClassName);

  return <h1 className={titleClasses}>{text}</h1>;
};

Title.defaultProps = {
  extraClassName: '',
};
