import classNames from 'classnames';
import { useContext } from 'react';

import { ProductCardDetails } from './ProductCardDetails';
import { Product } from '../../../helpers/types/Product';
import { useLocalStorage } from '../../../helpers/LocalStorage';
import {
  CART_KEY, FAVORITES_KEY,
} from '../../../helpers/constants/StorageKeys';
import {
  FavoritesCountContext,
} from '../../../helpers/context/FavoritesCountContext';

type ProductCardProps = {
  product: Product
};

function format(mbValue: string) {
  return `${mbValue.split('MB')[0]} ${'MB'}`;
}

export const ProductCard = ({
  product,
}: ProductCardProps) => {
  const [
    getCartProducts,
    setCartProducts,
    cartContains,
  ] = useLocalStorage(CART_KEY);

  const [
    getFavoriteProducts,
    setFavoriteProducts,
    favoritesContains,
  ] = useLocalStorage(FAVORITES_KEY);

  const { increment, decrement } = useContext(FavoritesCountContext);

  const {
    imageUrl,
    name,
    price,
    discount,
    screen,
    capacity,
    ram,
    id,
  } = product;

  const discountAmount = (discount / 100) * price;
  const newPrice = `$${price - discountAmount}`;
  const olddPrice = `$${price}`;
  const displayedScren = screen.replace(' inches', 'â€');
  const displayedCapacity = format(capacity);
  const displayedRam = format(ram);

  const inCart = cartContains(id);
  const buttonCartText = inCart ? 'Added to cart' : 'Add to cart';
  const buttonCartClass = classNames('product-card__cart', {
    'product-card__cart--clicked': cartContains(id),
  });
  const textCartClass = classNames({
    'product-card__cart-text': !cartContains(id),
    'product-card__cart-text--clicked': cartContains(id),
  });

  const favoritesImagePath = favoritesContains(id)
    ? 'img/favorites/heart-selected.svg'
    : 'img/favorites/heart.svg';

  const handleCartClick = () => {
    if (!cartContains(id)) {
      setCartProducts([...getCartProducts(), product]);
    }
  };

  const handleFavoritesClick = () => {
    if (favoritesContains(id)) {
      setFavoriteProducts(
        [...getFavoriteProducts().filter(favorite => favorite.id !== id)],
      );
      decrement();
    } else {
      setFavoriteProducts([...getFavoriteProducts(), product]);
      increment();
    }
  };

  return (
    <li className="product-card">
      <img
        className="product-card__image"
        src={imageUrl}
        alt="Product"
      />

      <p className="product-card__name">{name}</p>

      <div className="product-card__prices">
        <h2 className="product-card__current-price">{newPrice}</h2>

        {discount > 0 && (
          <div className="product-card__previous-price">{olddPrice}</div>
        )}
      </div>

      <div className="product-card__details">
        <ProductCardDetails name="screen" value={displayedScren} />

        <ProductCardDetails name="capacity" value={displayedCapacity} />

        <ProductCardDetails name="ram" value={displayedRam} />
      </div>

      <footer className="product-card__buttons">
        <button
          className={buttonCartClass}
          type="button"
          onClick={handleCartClick}
        >
          <span className={textCartClass}>{buttonCartText}</span>
        </button>

        <button
          className="product-card__favorites"
          type="button"
          onClick={handleFavoritesClick}
          data-cy="addToFavorite"
        >
          <img
            className="product-card__heart"
            src={favoritesImagePath}
            alt="Add to favorites"
          />
        </button>
      </footer>
    </li>
  );
};
