import { Cart } from './image-link/Cart';
import { Favourites } from './image-link/Favourites';
import { Logo } from '../Logo';
import { Nav } from './Nav';
import { Search } from './Search';

type HeaderProps = {
  hasSearch: boolean
  currentPage: string
  favoritesCount?: number
  cartCount?: number
};

export const Header = ({
  hasSearch,
  currentPage,
  favoritesCount,
  cartCount,
}: HeaderProps) => (
  <header id="header" className="header">
    <div className="header__part header__part--left">
      <Logo imageClasses="header__logo logo" />

      <Nav />
    </div>

    <div className="header__part">
      {hasSearch && <Search currentPage={currentPage} />}

      <Favourites count={favoritesCount} />

      <Cart count={cartCount} />
    </div>
  </header>
);

Header.defaultProps = {
  favoritesCount: -1,
  cartCount: -1,
};
